.tab-review {
	position: absolute;
	top:114px;
	left:318px;
	width:382px;
	background: #fff;
	box-sizing:border-box;
	padding:30px;

	@media (max-width:$tablet-width) {
		top:80px;
		left:calc(50% - 191px);
	}

	@media (max-width:$mobile-width-only) {
		width:268px;
		height:431px;
		display: flex;
		flex-direction:column;
		align-items:center;
		padding:30px 20px 34px 20px;
		top:50px;
		left:calc(50% - 134px);
	}
}
.tab-review__name {
	position: relative;
	font-weight: 500;
	font-size: 30px;
	line-height: 37px;
	letter-spacing: -0.691667px;
	color: #2C2E3F;
	margin:0;

	&:after {
		content: "";
		position: absolute;
		bottom:-10px;
		left:0;
		width:68px;
		height:1px;
		background: $button__accent;
	}

	@media (max-width:$mobile-width-only) {
		font-size: 20px;
		line-height: 24px;

		&:after {
			left:calc(50% - 34px);
		}
	}
}
.tab-review__text {
	font-size: 14px;
	line-height: 28px;
	letter-spacing: -0.408333px;
	color: rgba(44, 46, 63, 0.7);
	margin:32px 0 0 0;

	@media (max-width:$mobile-width-only) {
		text-align:center;
	}
}
.tab-review__author {
	position: relative;
	width:auto;
	height:38px;
	box-sizing:border-box;
	padding:0 0 0 53px;
	margin:35px 0 0 0;
	font-weight: 500;
	font-size: 16px;
	line-height: 38px;
	letter-spacing: -0.466667px;

	&:before {
		content:"";
		position: absolute;
		top:0;
		left:0;
		width:38px;
		height:38px;
		border-radius:50%;
		background-image:url("../img/greeceReviewPhoto.png");
		background-repeat: no-repeat;
		background-size: 100% 100%;
	}

	@media (max-width:$mobile-width-only) {
		margin:68px 0 0 0;
		padding:0;
		text-align:center;
		&:before {
			top:-48px;
			left:calc(50% - 19px);
		}
	}
}