.tab-info {
	width:425px;
	margin:6px 45px 0 0;

	@media (max-width:$tablet-width) {
		display: flex;
		flex-direction:column;
		align-items:center;
		width:726px;
		margin:0;
	}

	@media (max-width:$mobile-width-only) {
		width:320px;
	}
}
.tab-info__name {
	position: relative;
	font-weight: 600;
	font-size: 55px;
	line-height: 90px;
	letter-spacing: -1.245px;
	margin:0;

	&:after {
		content:"";
		position: absolute;
		bottom:-20px;
		left:0;
		width:175px;
		height:3px;
		background:$button__accent;
	}

	@media (max-width:$tablet-width) {
		font-size: 30px;
		line-height: 150%;

		&:after {
			content:none;
		}
	}

	@media (max-width:$mobile-width-only) {
		font-size:24px;
	}
}
.tab-info__text {
	width:370px;
	font-size: 16px;
	line-height: 28px;
	letter-spacing: -0.466667px;
	color: rgba(44, 46, 63, 0.7);
	margin:53px 0 0 0;

	@media (max-width:$tablet-width) {
		width:726px;
		text-align: center;
		margin:20px 0 0 0;
	}

	@media (max-width:$mobile-width-only) {
		width:288px;
	}
}
.tab-info__button {
	width:250px;
	height:75px;
	display:flex;
	justify-content:center;
	align-items:center;
	font-weight:700;
	font-size: 12px;
	line-height: 15px;
	letter-spacing: 2px;
	text-transform: uppercase;
	background:$button__accent;
	color:#fff;
	border:none;
	margin:50px 0 0 0;

	&:hover {
		background: $button__hover;
	}

	@media (max-width:$tablet-width) {
		height:50px;
		font-size: 10px;
		line-height: 12px;
		margin:20px 0 0 0;
	}

	@media (max-width:$mobile-width-only) {
		width:288px;
	}
}